const initialState = {  
    //  load order
    orders: [],
    loading: false, 
    error: null,

    // save order
    newOrder: {
        saving: false, 
        finihsed: false, 
        error: null
    }
}

// reducer catches the action has been dispatched
const reducer = (state = initialState, action) => {   
    
    switch(action.type) {
        case "LOAD_ORDERS_ACTIONS" : return {
            ...state, 
            loading: true
        };
        case "LOAD_ORDERS_SUCCESS" : return {
            ...state, 
            loading: false,
            orders: action.orders
        }
        case "LOAD_ORDERS_ERROR" : return {
            ...state, 
            loading: false,
            error: action.error            
        }

        case "SAVE_ORDER_START" : 
            return {
                ...state, 
                newOrder: {
                    ...state.newOrder,
                    saving: true
                }
            }
        
        case "SAVE_ORDER_SUCCESS" : 
            return {
                ...state, 
                newOrder: {
                    ...state.newOrder,
                    saving: false, 
                    finished: true,
                    error: null
                }
            }

        case "SAVE_ORDER_ERROR" : 
            return {
                ...state, 
                newOrder: {
                    ...state.newOrder,
                    saving: false, 
                    finished: true,
                    error: action.error
                }
            }
        default : 
            return state;
    }

}

export default reducer;